import React from "react";
import styled from "styled-components";
import { pages } from "../const/pages";
import Slider from "../components/organisms/Slider";
import Title from "../components/atoms/Title";
import Footer from "../components/organisms/Footer";
import Colaboration from "../components/organisms/Colaboration";
import homeColab from "../assets/images/homeColab.jpg";
import footer from "../assets/images/footer.jpg";
import Button from "../components/atoms/Button";

const StyledTitlesWrapper = styled.div`
  padding: 5rem 20rem;
  background-image: url(${footer});
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  min-height: 80vh;
  text-align: center;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
  }
`;

const StyledTitle = styled(Title)`
  margin: 0 0 3rem;
`;

const HomeTemplate = () => (
  <>
    <Slider location={pages.glowna.location} />
    <StyledTitlesWrapper>
      <StyledTitle small orange>
        OneSerwis dzięki swojej wiarygodności i szerokiej ofercie ma możliwość
        współpracować z największymi markami z rynku motoryzacyjnego na świecie
        jak ADAC.
      </StyledTitle>
      <StyledTitle small orange>
        Sieć warsztatów OneSerwis oferuje swoim użytkownikom niepowtarzalne
        możliwości zwiększenia renomy warsztatu oraz skuteczności i szybkości
        napraw.
      </StyledTitle>
      <Button>Czytaj więcej...</Button>
    </StyledTitlesWrapper>
    <Colaboration home img={homeColab} />
    <Footer />
  </>
);

export default HomeTemplate;
